import React, {Component} from 'react';
import {HashRouter as Router, Route} from 'react-router-dom';

import GoogleAnalyticsHOC from './GoogleAnalyticsHOC'
import Header from "./Header";
import Main from "./Main";
import Welcome from "./Welcome";
import Footer from "./Footer";
import Troubleshooting from "./Troubleshooting";
import About from "./About";
import TermsOfService from './TermsOfService'

class App extends Component {
  render() {
    return (
        <Router>
          <div className="d-flex flex-column wrapper">
            <Header/>
            <main className="container flex-fill">
              <Route exact path="/" component={GoogleAnalyticsHOC(Main)}/>
              <Route exact path="/about" component={GoogleAnalyticsHOC(About)}/>
              <Route path="/welcome" component={GoogleAnalyticsHOC(Welcome)}/>
              <Route path="/troubleshooting" component={GoogleAnalyticsHOC(Troubleshooting)}/>
              <Route path="/terms-of-service" component={GoogleAnalyticsHOC(TermsOfService)}/>
            </main>
            <Footer/>
          </div>
        </Router>
    );
  }
}

export default App;



