import React, {Component} from 'react';
import {OutboundLink} from 'react-ga';

class Troubleshooting extends Component {
  render() {
    return (
        <div className="row">
          <div className="col">
            <h1>Troubleshooting</h1>
            <div className="alert alert-danger" role="alert">
              DepShield is available for Maven Java and npm Node.js projects where a pom.xml or package.json file exists 
              in the root directory of the master branch. DepShield communicates all information through its badge and GitHub
              Issues. Be sure to enable GitHub Issues to receive security alerts from DepShield.
            </div>
            <h2>DepShield encountered errors while building your project</h2>
            <p>
              If DepShield was unable to build your project with Maven or npm, a GitHub Issue will be created with a link to
              the error message produced by Maven or npm. Ensure that the project can be built locally with the default
              repositories and configuration.
            </p>
            <h2>Badge stuck in processing</h2>
            <p>
              Most error cases should result in an error Issue being generated for your project. The badge may become
              stuck in a processing state if:
            </p>
            <ul>
              <li>GitHub Issues are disabled on the project and DepShield encounters an issue</li>
              <li>If DepShield in uninstalled and reinstalled the badge will be stuck on processing until a change to
                the pom.xml file has been made and DepShield beings completes its reprocessing.
              </li>
              <li>If a repository if forked from an existing repository with DepShield enabled the badge will be stuck
                on processing until a change to the pom.xml file has been made and DepShield beings completes its
                reprocessing.
              </li>
            </ul>
            <h2>Think something is missing</h2>
            <p>
              In an effort to maximize the signal to noise ratio, DepShield currently reports on Critical and High
              security vulnerabilities. If you believe this should be tweaked, let us know in the <OutboundLink
                to="//community.sonatype.com/c/depshield" eventLabel="//community.sonatype.com/c/depshield"
                target="_blank">Sonatype DepShield Community</OutboundLink> and investigate <OutboundLink
                to="//www.sonatype.com/nexus-lifecycle" eventLabel="//www.sonatype.com/nexus-lifecycle" target="_blank">Sonatype
              Nexus Lifecycle</OutboundLink> which provides robust policy configuration.
            </p>
            <h2>Other feedback or issues?</h2>
            <p>
              If you have any feedback or issues that are not covered here, please join us at the <OutboundLink
                to="//community.sonatype.com/c/depshield" eventLabel="//community.sonatype.com/c/depshield"
                target="_blank">Sonatype DepShield Community</OutboundLink>.
            </p>
          </div>
        </div>
    );
  }
}

export default Troubleshooting;



