import React, {Component} from 'react';
import { Link } from 'react-router-dom';
import { OutboundLink } from 'react-ga';

import './Footer.css';

import sonatypeLogo from './images/sonatype-logo-full.png';

class Footer extends Component {
  render() {
    return (
        <footer className="footer container-fluid py-5 mt-3 bg-light">
          <div className="container">
            <div className="row">
              <div className="col-12 col-md">
                <img src={sonatypeLogo} height="24" alt="" />
                <small className="d-block mb-3 text-muted">&copy; 2018-present, Sonatype Inc.</small>
              </div>
              <div className="col-6 col-md">
                <h5>About</h5>
                <ul className="list-unstyled text-small">
                  <li><OutboundLink className="text-muted" eventLabel="//sonatype.com" to="//sonatype.com"
                                    target="_blank">
                    <small><i className="fas fa-external-link-alt"/></small>
                    Sonatype</OutboundLink></li>
                  <li><OutboundLink className="text-muted" to="//community.sonatype.com/c/depshield"
                                    eventLabel="//community.sonatype.com/c/depshield" target="_blank">DepShield
                    Community</OutboundLink></li>
                </ul>
              </div>
              <div className="col-6 col-md">
              </div>
              <div className="col-6 col-md">
                <h5>Legal</h5>
                <ul className="list-unstyled text-small">
                  <li>
                  <Link className="text-muted" to="/terms-of-service">Terms of Service</Link>
                  </li>
                  <li><OutboundLink className="text-muted" eventLabel="//www.sonatype.com/privacy-policy"
                                    to="//www.sonatype.com/privacy-policy" target="_blank">
                    <small><i className="fas fa-external-link-alt"/></small>
                    Privacy Policy</OutboundLink></li>
                </ul>
              </div>
            </div>
          </div>
        </footer>
    );
  }
}

export default Footer;



