import React, {Component} from 'react';
import {CopyToClipboard} from 'react-copy-to-clipboard';
import {OutboundLink} from 'react-ga';

import './Welcome.css';

import badgeScreenshot from './images/screenshot-zero-vulnerable.png';
import issueScreenshot from './images/screenshot-issue.png';
import transitiveScreenshot from './images/screenshot-transitive.png';

const badgeMarkdown = '[![DepShield Badge](https://depshield.sonatype.org/badges/owner/repository/depshield.svg)](https://depshield.github.io)';

class Welcome extends Component {
  render() {
    return (
        <div className="row">
          <div className="col">
            <h1>Congratulations on Installing Sonatype DepShield</h1>
            <p className="lead text-muted">
              DepShield is currently evaluating your subscribed repositories for vulnerabilities in their open source dependencies.
              Processing should take on the order of minutes per repository. While you wait, why not add a DepShield badge to your
              repositories to show that you are being protected?
            </p>
            <h2>DepShield Badge</h2>
            <p>
              The DepShield badge is used to indicate the number of vulnerable components in your repository. Work on resolving GitHub
              Issues generated by DepShield to reduce this number to zero and then wear the badge on your README with pride. The badge 
              can be added to your README using the Markdown code below, substituting <code>{'owner'}</code> with your GitHub username
              or organization and <code>{'repository'}</code> with your repository name.
            </p>
            <p>
              <img src={badgeScreenshot} className="img-fluid mx-auto d-block" width="156px" alt="DepShield Badge" />
            </p>
            <figure className="code">
              <CopyToClipboard text={badgeMarkdown}>
                <button type="button" className="btn btn-light copy">Copy</button>
              </CopyToClipboard>
              <pre>
                <code>
                  {badgeMarkdown}
                </code>
              </pre>
            </figure>
            <h2>DepShield Issues</h2>
            <p>
              In order to help you remediate security vulnerabilities in your open source components, DepShield will generate
              GitHub Issues for each vulnerable component in your project. The Issue will list all security advistories applicable 
              to that component. Using information found on the linked <OutboundLink to="//ossindex.sonatype.org"
                                                                          eventLabel="//ossindex.sonatype.org"
                                                                          target="_blank">Sonatype OSS Index</OutboundLink>, the 
              project maintainer can remediate the security vulnerabilities by choosing a non-vulnerable version of that component.
            </p>
            <p>
              <img src={issueScreenshot} className="img-fluid mx-auto d-block" width="600px" alt="DepShield Issue" />
            </p>
            <h2>Transitive Components</h2>
            <p>
              Not all components are brought in directly by your application. These transitive components may have security
              vulnerabilities as applicable to your project as any direct dependency. The Occurances section is available on DepShield
              Issues for transitive dependencies. The Occurances section allows you a view into which dependency brought in the
              vulnerable component, allowing you to make a more informed decision as to how to remediate.
            </p>
            <p>
              <img src={transitiveScreenshot} className="img-fluid mx-auto d-block" width="600px" alt="DepShield Transitive Issue" />
            </p>
          </div>
        </div>
    );
  }
}

export default Welcome;



