import React, {Component} from 'react';
import { Link } from 'react-router-dom';

import './Header.css';

import logo from './images/icon-white.png';

class Header extends Component {
  render() {
    return (
        <header>
          <nav className="navbar fixed-top navbar-expand-lg navbar-dark bg-black">
            <div className="container-fluid">
              <Link className="navbar-brand navbar-depshield" to="/">
                <img className="d-inline-block align-top mr-2" src={logo}
                     height="32" alt=""/>
              </Link>
              <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbar-toggle"
                      aria-controls="navbar-toggle" aria-expanded="false" aria-label="Toggle navigation">
                <span className="navbar-toggler-icon" />
              </button>
              <div className="collapse navbar-collapse" id="navbar-toggle">
                <ul className="navbar-nav mr-auto">
                  <li className="nav-item"> <Link className="nav-link" to="/">Home</Link>
                  </li>
                  <li className="nav-item"> <Link className="nav-link" to="/about">About</Link>
                  </li>
                  <li className="nav-item"> <Link className="nav-link" to="/welcome">Getting Started</Link>
                  </li>
                  <li className="nav-item"> <Link className="nav-link" to="/troubleshooting">Troubleshooting</Link>
                  </li>
                </ul>
              </div>
            </div>
          </nav>
        </header>
    );
  }
}

export default Header;



