import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {OutboundLink} from 'react-ga';

import './Main.css'
import logo from './images/logo.png';
import gitHubCircle from './images/circle-github.png';
import continuousCircle from './images/circle-continuous-monitoring.png';
import availableCircle from './images/circle-available-languages.png';
import issueScreenshot from './images/screenshot-issue.png';
import issueListScreenshot from './images/screenshot-issue-list.png';
import ossIndexScreenshot from './images/screenshot-oss-index.png';
import repositoryLogo from './images/nexus-repository-128x128.png';
import lifecycleLogo from './images/nexus-lifecycle-128x128.png';
import firewallLogo from './images/nexus-firewall-128x128.png';

class Main extends Component {
  render() {
    return (
        <div>
          <div className="jumbotron">
            <h1 className="display-4 welcome-brand">
              <img src={logo} height="64" width="220"/>
            </h1>
            <p className="lead text-muted">
              Sonatype DepShield is a GitHub App used by developers to identify and remediate vulnerabilities in their
              open source dependencies.
            </p>
            <p>
              <OutboundLink to="//github.com/apps/sonatype-depshield" eventLabel="//github.com/apps/sonatype-depshield"
                            className="btn btn-cta" role="button">
                Install DepShield on your GitHub repositories
              </OutboundLink>
            </p>
          </div>
          <div>
            <div className="container-fluid mb-4">
              <div className="row">
                <div className="col-md">
                  <div className="row justify-content-center">
                    <img className="mb-2" src={gitHubCircle} height="128" width="128" />
                  </div>
                  <p>
                    Powered by <OutboundLink to="//ossindex.sonatype.org" eventLabel="//ossindex.sonatype.org"
                                             target="_blank">Sonatype OSS Index</OutboundLink>. Free for public and
                    private GitHub repos.
                  </p>
                </div>
                <div className="col-md">
                  <div className="row justify-content-center">
                    <img className="mb-2" src={continuousCircle} height="128" width="128" />
                  </div>
                  <p>
                    Continuously monitors projects and auto-creates issues for security vulnerabilities.
                  </p>
                </div>
                <div className="col-md">
                  <div className="row justify-content-center">
                    <img className="mb-2" src={availableCircle} height="128" width="128" />
                  </div>
                  <p>
                    Available for Apache Maven and Node.js npm projects.
                  </p>
                </div>
              </div>
            </div>
            <div className="gray-table">
              <div className="container">
                <div className="container-fluid pt-4 mb-4">
                  <div className="row mb-4">
                    <div className="col-md mb-4">
                      <img className="img-fluid" src={issueListScreenshot} width="512px" alt="DepShield Issue List" />
                    </div>
                    <div className="col-lg">
                        View a list of known security vulnerabilities within GitHubs Issue Tracker.
                    </div>
                  </div>
                  <div className="row-divider" />
                  <div className="row mb-4">
                    <div className="col-md mb-4">
                      <img className="img-fluid" src={issueScreenshot} width="512px" alt="DepShield Issue List" />
                    </div>
                    <div className="col-lg">
                      Click on issue to view vulnerability details including CVE and CVSS.
                    </div>
                  </div>
                  <div className="row-divider" />
                  <div className="row mb-4">
                    <div className="col-md mb-4">
                      <img className="img-fluid" src={ossIndexScreenshot} width="512px" alt="DepShield Issue List" />
                    </div>
                    <div className="col-lg">
                      Determine vulnerable version ranges on each vulnerability.
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <h2>DepShield is powered by Sonatype OSS Index</h2>
            <p>
              Sonatype DepShield is powered by <OutboundLink to="//ossindex.sonatype.org"
                                                             eventLabel="//ossindex.sonatype.org" target="_blank">Sonatype
              OSS Index</OutboundLink> which is based on vulnerability data derived from public sources and does not
              include human curated intelligence nor expert remediation guidance. Software development teams with
              requirements for fully automated open source governance powered by precise, curated, and actionable
              intelligence should investigate the <OutboundLink to="//www.sonatype.com/products-overview"
                                                                eventLabel="//www.sonatype.com/products-overview"
                                                                target="_blank">Nexus Platform</OutboundLink>.
            </p>
            <div className="container-fluid">
              <div className="row">
                <div className="col-md mb-4">
                  <div className="row justify-content-center">
                    <OutboundLink to="//www.sonatype.com/nexus-firewall" eventLabel="//www.sonatype.com/nexus-firewall"
                                  target="_blank">
                      <img className="mb-2" src={firewallLogo} height="64" width="64" />
                    </OutboundLink>
                  </div>
                  <div className="row justify-content-center">
                    <h4>
                      <OutboundLink to="//www.sonatype.com/nexus-firewall"
                                    eventLabel="//www.sonatype.com/nexus-firewall" target="_blank">Nexus Firewall
                      </OutboundLink>
                    </h4>
                  </div>
                  <div className="row justify-content-center">
                    <p className="text-muted">Stop bad parts at the front door.</p>
                  </div>
                  <div className="row justify-content-center">
                    <OutboundLink className="btn btn-outline-secondary" to="//www.sonatype.com/nexus-firewall"
                                  eventLabel="//www.sonatype.com/nexus-firewall" target="_blank" role="button">Learn
                      More</OutboundLink>
                  </div>
                </div>
                <div className="col-md mb-4">
                  <div className="row justify-content-center">
                    <OutboundLink to="//www.sonatype.com/nexus-lifecycle"
                                  eventLabel="//www.sonatype.com/nexus-lifecycle" target="_blank">
                      <img className="mb-2" src={lifecycleLogo} height="64" width="64" />
                    </OutboundLink>
                  </div>
                  <div className="row justify-content-center">
                    <h4>
                      <OutboundLink to="//www.sonatype.com/nexus-lifecycle"
                                    eventLabel="//www.sonatype.com/nexus-lifecycle" target="_blank">Nexus
                        Lifecycle</OutboundLink>
                    </h4>
                  </div>
                  <div className="row justify-content-center">
                    <p className="text-muted">Continuously analyze quality and security.</p>
                  </div>
                  <div className="row justify-content-center">
                    <OutboundLink className="btn btn-outline-secondary" to="//www.sonatype.com/nexus-lifecycle"
                                  eventLabel="//www.sonatype.com/nexus-lifecycle" target="_blank" role="button">Learn
                      More</OutboundLink>
                  </div>
                </div>
                <div className="col-md mb-4">
                  <div className="row justify-content-center">
                    <OutboundLink to="//www.sonatype.com/nexus-repository-sonatype"
                                  eventLabel="//www.sonatype.com/nexus-repository-sonatype" target="_blank">
                      <img className="mb-2" src={repositoryLogo} height="64" width="64" />
                    </OutboundLink>
                  </div>
                  <div className="row justify-content-center">
                    <h4>
                      <OutboundLink to="//www.sonatype.com/nexus-repository-sonatype"
                                    eventLabel="//www.sonatype.com/nexus-repository-sonatype" target="_blank">Nexus
                        Repository</OutboundLink></h4>
                  </div>
                  <div className="row justify-content-center">
                    <p className="text-muted">Organize, store, and distribute parts.</p>
                  </div>
                  <div className="row justify-content-center">
                    <OutboundLink className="btn btn-outline-secondary"
                                  to="//www.sonatype.com/nexus-repository-sonatype"
                                  eventLabel="//www.sonatype.com/nexus-repository-sonatype" target="_blank"
                                  role="button">Learn More</OutboundLink>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    );
  }
}

export default Main;



